import React, { useState } from 'react';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { RepleUploadDiv } from '../../Style/RepleCSS.js';

function RepleUpload(props) {
  const [reple, setReple] = useState('');
  const user = useSelector(state => state.user);

  const submitHandler = e => {
    e.preventDefault();
    if (!reple) {
      return alert('  ');
    }
    let body = {
      reple: reple,
      uid: user.uid,
      postId: props.postId,
    };
    axios.post('/api/reple/submit', body).then(res => {
      setReple('');
      if (res.data.success) {
        alert('  .');
        window.location.reload();
      } else {
        alert('  .');
      }
    });
  };
  return (
    <RepleUploadDiv>
      <form>
        <input
          type="text"
          value={reple}
          onChange={e => setReple(e.target.value)}
        />
        <button onClick={e => submitHandler(e)}> </button>
      </form>
    </RepleUploadDiv>
  );
}

export default RepleUpload;
