import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { LoginDiv } from '../../Style/UserCSS';
import firebase from '../../firebase.js';
import axios from 'axios';

function Register() {
  const navigate = useNavigate();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [passwordConfirm, setPasswordConfirm] = useState('');
  const [flag, setFlag] = useState(false);
  //    axio  
  const [nameCheck, setNameCheck] = useState(false);
  //    
  const [nameInfo, setNameInfo] = useState('');
  //firebase     app   
  //async await   

  const RegisterHandler = async e => {
    if (!flag) {
      setFlag(false);
    } else {
      setFlag(true);
    }

    e.preventDefault();
    if (!(name && email && password && passwordConfirm)) {
      return alert('  ');
    }
    if (!email.includes('@')) {
      return alert('    ');
    }
    if (password !== passwordConfirm) {
      return alert('     ');
    } else if (password.length < 6) {
      return alert('  6   .');
    }
    if (!nameCheck) {
      return alert('   ');
    }

    let createdUser = await firebase
      .auth()
      .createUserWithEmailAndPassword(email, password);
    await createdUser.user.updateProfile({
      displayName: name,
      photoURL: `https://i.pinimg.com/550x/18/b9/ff/18b9ffb2a8a791d50213a9d595c4dd52.jpg`,
    });

    let body = {
      email: createdUser.user.multiFactor.user.email,
      displayName: createdUser.user.multiFactor.user.displayName,
      uid: createdUser.user.multiFactor.user.uid,
      photoURL: `https://i.pinimg.com/550x/18/b9/ff/18b9ffb2a8a791d50213a9d595c4dd52.jpg`,
    };
    axios.post('/api/user/register', body).then(res => {
      setFlag(false);
      if (res.data.success) {
        // 
        alert('  ');
        navigate('/login');
      } else {
        // 
        alert('  ');
      }
    });
  };

  const nameCheckHandler = e => {
    e.preventDefault();
    if (!name) {
      alert('  .');
      nameCheck(true);
    }
    let body = {
      displayName: name,
    };
    axios.post('/api/user/namecheck', body).then(res => {
      if (res.data.success) {
        if (res.data.check) {
          setNameCheck(true);
          setNameInfo('  .');
        } else {
          setNameInfo(' .');
        }
      }
    });
  };

  return (
    <LoginDiv>
      <form>
        <label htmlFor=""> </label>
        <input
          type="name"
          value={name}
          onChange={e => setName(e.target.value)}
          disabled={nameCheck}
        />
        {nameInfo}
        <button onClick={e => nameCheckHandler(e)}>   </button>
        <label htmlFor=""> </label>
        <input
          type="email"
          value={email}
          onChange={e => setEmail(e.target.value)}
        />
        <label htmlFor=""> </label>
        <input
          minLength={6}
          type="password"
          value={password}
          onChange={e => setPassword(e.target.value)}
        />
        <label htmlFor="">  </label>
        <input
          minLength={6}
          type="password"
          value={passwordConfirm}
          onChange={e => setPasswordConfirm(e.target.value)}
        />
        <button type="submit" onClick={e => RegisterHandler(e)} disabled={flag}>
          
        </button>
      </form>
    </LoginDiv>
  );
}

export default Register;
