import axios from 'axios';
import React from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { PostBox, BtnDiv, PostDiv } from '../../Style/DetailListCSS';
import { useSelector } from 'react-redux';
import Avartar from 'react-avatar';
import moment from 'moment';
import 'moment/locale/ko';

function Detail(props) {
  const params = useParams();
  const navigate = useNavigate();
  const user = useSelector(state => state.user);

  const DeleteHandler = () => {
    if (window.confirm('    ?')) {
      let body = {
        postNum: params.postNum,
      };
      axios
        .post('/api/post/delete', body)
        .then(res => {
          if (res.data.success) {
            alert(' ');
            navigate('/');
          }
        })
        .catch(err => {
          alert('     ');
        });
    } else {
      alert(' ');
    }
  };

  const setTime = (a, b) => {
    if (a !== b) {
      return moment(b).format('YYYY MMMM Do, hh:mm') + '()';
    } else {
      return moment(a).format('YYYY MMMM Do, hh:mm');
    }
  };

  return (
    <PostDiv>
      <PostBox>
        <h1>{props.postInfo.title} </h1>
        <div className="author">
          <Avartar
            round={true}
            size="40"
            src={`https://react-board-community.herokuapp.com/${props.postInfo.author.photoURL}`}
            style={{ border: '1px solid #c6c6c6' }}
          />
          <p> {props.postInfo.author.displayName}</p>
          <p className="time">
            {setTime(props.postInfo.createdAt, props.postInfo.updatedAt)}
          </p>
        </div>
        <div>
          {props.postInfo.image ? (
            <img
              style={{ width: '100%', height: 'auto' }}
              src={`https://react-board-community.herokuapp.com/${props.postInfo.image}`}
              alt={props.postInfo.image}
            />
          ) : null}
        </div>
        <div className="content">{props.postInfo.content}</div>
      </PostBox>
      {user.uid === props.postInfo.author.uid && (
        <BtnDiv>
          <Link to={`/edit/${props.postInfo.postNum}`}>
            <button className="edit"></button>
          </Link>
          <Link to="">
            <button
              className="delete"
              onClick={() => {
                DeleteHandler();
              }}
            >
              
            </button>
          </Link>
        </BtnDiv>
      )}
    </PostDiv>
  );
}

export default Detail;
