import React, { useState, useEffect } from 'react';
import axios from 'axios';
import List from './Post/List';
import { DropdownButton, Dropdown } from 'react-bootstrap';
import { GNBDiv, FooterDiv } from '../Style/MainPageCSS.js';
import { BiSearch } from 'react-icons/bi';

function MainPage() {
  const [postList, setPostList] = useState([]);
  const [sort, setSort] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [skip, setSkip] = useState(0);
  const [loadMore, setLoadMore] = useState(true);

  //  
  const getPostLoadMore = () => {
    let body = {
      sort,
      searchTerm,
      skip,
    };
    axios
      .post('/api/post/list', body)
      .then(res => {
        const list = res.data.postList;
        if (res.data.success) {
          setPostList([...postList, ...list]);
          setSkip(skip + list.length);
          //[0] ~[4] : 5 skip
          //[5] ~[9]
          //[10] ~[14] : 10skip
          if (list.length < 5) {
            setLoadMore(false);
          }
        }
      })
      .catch(err => {
        alert(`    ${err}`);
      });
  };

  //   
  const getPostList = () => {
    setSkip(0);
    let body = {
      sort,
      searchTerm,
      skip: 0,
    };
    axios
      .post('/api/post/list', body)
      .then(res => {
        const list = res.data.postList;
        if (res.data.success) {
          setPostList([...list]);
          setSkip(list.length);
          //[0] ~[4] : 5 skip
          //[5] ~[9]
          //[10] ~[14] : 10skip
          if (list.length < 5) {
            setLoadMore(false);
          }
        }
      })
      .catch(err => {
        alert(`    ${err}`);
      });
  };

  useEffect(() => {
    getPostList();
  }, [sort]);

  const SearchHandler = () => {
    //     
    // if (searchTerm === '') {
    //   return;
    // }

    //    
    getPostList();
  };

  return (
    <div>
      <GNBDiv>
        <div className="search">
          <input
            type="text"
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
            onKeyDown={e => {
              if (e.keyCode === 13) SearchHandler();
            }}
          />
          <button
            onClick={() => {
              SearchHandler();
            }}
          >
            <BiSearch className="bi bi-search" />
          </button>
        </div>

        <DropdownButton
          variant="outline-secondary"
          title={sort}
          id="input-group-dropdown-1"
        >
          <Dropdown.Item onClick={() => setSort('')}>
            
          </Dropdown.Item>
          <Dropdown.Item onClick={() => setSort('')}>
            
          </Dropdown.Item>
        </DropdownButton>
      </GNBDiv>
      <List postList={postList} />

      {/*     */}
      {loadMore && (
        <FooterDiv>
          <button
            style={{ marginBottom: '10vh' }}
            onClick={() => getPostLoadMore()}
          >
             
          </button>
        </FooterDiv>
      )}
    </div>
  );
}

export default MainPage;
