import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import Avartar from 'react-avatar';
import axios from 'axios';
import firebase from '../../firebase.js';
import { MyPageDiv } from '../../Style/UserCSS.js';

function MyPage() {
  const [currentImage, setCurrentImage] = useState('');
  const navigate = useNavigate();
  const user = useSelector(state => state.user);

  useEffect(() => {
    if (user.isLoading && !user.accessToken) {
      navigate('/login');
    } else {
      setCurrentImage(user.photoURL);
    }
  }, [user]);

  const ImageUpload = e => {
    let formData = new FormData();
    formData.append('file', e.target.files[0]);
    axios.post('/api/user/profile/img', formData).then(response => {
      setCurrentImage(response.data.filePath);
    });
  };
  const saveProfile = async e => {
    e.preventDefault();
    try {
      await firebase.auth().currentUser.updateProfile({
        photoURL: currentImage,
      });
    } catch (err) {
      return alert('  .');
    }
    let body = {
      photoURL: currentImage,
      uid: user.uid,
    };
    axios.post('/api/user/profile/update', body).then(res => {
      if (res.data.success) {
        alert('  .');
        window.location.reload();
      } else {
        alert('  .');
      }
    });
  };

  return (
    <MyPageDiv>
      <form
        style={{
          width: '50%',
          margin: '0 auto',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          marginTop: '2rem',
        }}
      >
        <label>
          <input
            type="file"
            accept="image/*"
            style={{ display: 'none' }}
            onChange={e => ImageUpload(e)}
          />
          <Avartar
            round={true}
            size="100"
            src={`https://react-board-community.herokuapp.com/${currentImage}`}
            style={{ border: '1px solid #c6c6c6', cursor: 'pointer' }}
          />
        </label>
        <button
          onClick={e => {
            saveProfile(e);
          }}
        >
          {' '}
          
        </button>
      </form>
    </MyPageDiv>
  );
}

export default MyPage;
